import React from 'react'
import { Typography, Card } from 'antd'
import ModalImage from "react-modal-image";
import {Link} from 'gatsby';
import LandingLayout from '@layouts/LandingLayout'


export default () => (
  <LandingLayout activeTab = '2'>
    <div className='flex flex-col items-start'>
    <Typography.Title className='text-primary-color'>
        WhatsInMyFood - Enforce your diet with a press of a button
      </Typography.Title>
    <div className="p-2 col-md-12 mt-4">
        <Link to='/projects/whatsinmyfood/privacy'>
      <button type='button' className="btn btn-dark">Privacy policy</button>
            </Link>
            &nbsp;&nbsp;
        <Link to='/projects/whatsinmyfood/tos'>
      <button type='button' className="btn btn-dark">Terms and Conditions</button>
            </Link>
            &nbsp;&nbsp;
        <Link to='/projects/whatsinmyfood/download'>
      <button type='button' className="btn btn-dark">Download now</button>
            </Link>
        </div>
        <br/>
     <Card 
     style={{ width: '100%' }}
     title="Dev Notes: First Release"
     //extra={<Link to="/projects/securet/1">Read more</Link>}
     >
     May 1, 2022 <br/> This is the first release for this app. I hope this app can have some form of impact to people
     like me who have strict dietary restrictions. 
     </Card>
    </div>
  </LandingLayout>
)
